@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  padding: 4rem;
  background-color: #212121;
  font-family: "Inter", sans-serif;
}

.title {
  color: #29b9f2;
  text-align: center;
  font-size: 3.8rem;
  font-weight: 600;
  margin-bottom: 3rem;
}

.expense-item__date {
  color: rgba(255, 255, 255, 0.8);
  min-width: 11rem;
  background-color: #3a3a3a;
  border-right: 0.25px solid rgba(255, 255, 255, 0.2);
  padding: 0.5rem;
  align-self: stretch;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.expense-item__date-month {
  font-size: 1.4rem;
  margin-bottom: 0.3rem;
}

.expense-item__date-year {
  font-size: 1.2rem;
}

.expense-item__date-day {
  font-size: 2rem;
  font-weight: 600;
  margin-top: 0.75rem;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6rem;
  font-weight: 500;
  background-color: #323232;
  color: rgba(255, 255, 255, 0.9);
  display: flex;
  overflow: hidden;
}

.expense-item:not(:last-child) {
  margin-bottom: 1.25rem;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
  margin: 1rem;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: right;
  flex: 1 1;
  color: currentColor;
}

.expense-item__price {
  min-width: 11rem;
  padding: 1.5rem 0;
  font-weight: 600;
  text-align: center;
  color: #fff;
  background-color: #22a2f2;
  margin-bottom: 1rem;
  border-radius: 10px;
  border: 0.5px solid rgba(255, 255, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .expense-item h2 {
    font-size: 2rem;
    margin-left: 1.5rem;
    text-align: left;
  }

  .expense-item__price {
    margin-bottom: 0;
  }
}

.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  border-radius: 12px;
}

.expenses {
  background-color: #242424;
  border: 0.5px solid rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
}

